import React from "react";

class CreatReactWithTable extends React.Component{
    constructor(props){
        super(props)
    }

    componentWillMount(){

    }

    render(){
        return (
            <div >
                <pre>{'import React, {Component} from \'react\';\n' +
                'import {Table} from \'antd\';\n' +
                '\n' +
                'class Home extends Component {\n' +
                '    constructor(props) {\n' +
                '        super(props);\n' +
                '        this.state = {\n' +
                '            pagination: {\n' +
                '                current: 1,\n' +
                '                pageSize: 10,\n' +
                '                total: 0,\n' +
                '                onChange: (a) => {\n' +
                '                    let pagination = this.state.pagination;\n' +
                '                    pagination.current = a;\n' +
                '                    this.setState({pagination}, this.getTable)\n' +
                '                },\n' +
                '            },\n' +
                '            data: [],\n' +
                '            columns: [\n' +
                '                {\n' +
                '                    title: \'id\',\n' +
                '                    dataIndex: \'id\',\n' +
                '                    key: \'id\',\n' +
                '                },\n' +
                '            ],\n' +
                '\n' +
                '        }\n' +
                '    }\n' +
                '\n' +
                '    componentWillMount() {\n' +
                '        this.getTable();\n' +
                '    }\n' +
                '    getTable() {\n' +
                '\n' +
                '    }\n' +
                '\n' +
                '    render() {\n' +
                '        return (\n' +
                '            <div>\n' +
                '                <Table\n' +
                '                    dataSource={this.state.data}\n' +
                '                    columns={this.state.columns}\n' +
                '                    pagination={this.state.pagination}\n' +
                '                    loading={this.state.tableLoading}\n' +
                '                />\n' +
                '            </div>\n' +
                '        );\n' +
                '    }\n' +
                '}\n' +
                '\n' +
                'export default Home;\n'}</pre>
            </div>
        )
    }
}
export default CreatReactWithTable;
